$style: levels;
.#{$style} {
  padding: 16px 0;
  &__head {
    @include flex-row;
    max-width: 30%;
    margin-bottom: 24px;
  }
  &__title {
    line-height: 1;
    flex: 1 0 140px;
  }
  &__select-wrapper {
    width: 90px;
    position: relative;
    margin-right: 12px;
  }
  &__disabled-option {
    color: $N6;
  }
  &__select {
    @extend %input;
    width: 90px;
  }
  &__input {
    @extend %input;
    width: 130px;
    &--custom {
      max-width: 100px;
      margin-right: 16px;
    }
    &--mr {
      margin-right: 12px;
    }
    &--like-select {
      width: 90px;
      position: absolute;
      top: 0;
      z-index: 1;
      pointer-events: none;
    }
  }
  &__distance-list {
    @include flex-row(flex-start, stretch);
    flex-wrap: wrap;
    width: 100%;
  }
  &__checkbox {
    // visibility: hidden;
    // opacity: 0;
    cursor: pointer;
    position: absolute;
    top: -12px;
    // &:hover, &:not(:checked) {
    //   visibility: visible;
    //   opacity: 1;
    // }
  }
  &__input-wrapper {
    @include flex-col;
    margin-right: 16px;
  }
  &__label {
    display: block;
    margin-bottom: 8px;
  }
  &__distance-item-box {
    position: relative;
    width: fit-content;
    @include flex-col(flex-start, center);
    @include media {
      // &:hover {
      //   .#{$style}__checkbox {
      //     visibility: visible;
      //     opacity: 1;
      //   }
      // }
    }
  }
  &__distance-item {
    padding: 0 16px;
    flex: 0 1 50%;
    min-width: 120px;
    @include flex-col(flex-start, center);
    margin-bottom: 20px;
    position: relative;
    @include media($screen-iphone-plus) {
      flex-basis: 33.3%;
    }
    @include media(520px) {
      flex-basis: 25%;
    }
    @include media(680px) {
      flex-basis: 20%;
    }
    @include media($screen-desktop) {
      flex-basis: 16.6%;
    }
    @include media($screen-hd) {
      flex-basis: 14.2%;
    }
    @include media($screen-fullhd) {
      flex-basis: 12.5%;
    }
    @include media($screen-retina) {
      flex-basis: 10%;
    }
  }
  &__inner {
    @include flex-row(stretch, stretch);
    width: 100%;
    &--custom {
      @include flex-col;
    }
  }
  &__col {
    flex-basis: 25%;
    max-width: 25%;
    @include flex-col(flex-start, center);
  }
  &__value {
    font-weight: 500;
    &--custom {
      margin-right: 16px;
    }
  }
  &__list {
    list-style: none;
  }
  &__item {
    margin-bottom: 6px;
    @include flex-row(flex-start, center);
  }
  &__icon {
    @include svg(20px, #4057FF);
    &--remove {
      transform: rotate(45deg);
    }
  }
  &__btn {
    background-color: transparent;
    border: none;
    font-size: 0;
    cursor: pointer;
    @include size(24px);
    @include flex-row(center, center);
    @include transition(filter);
    @include media {
      &:hover {
        filter: brightness(1.3);
      }
    }
    &:active {
      filter: brightness(0.9);
    }
  }
  &__row {
    @include flex-row(stretch, stretch);
    margin-bottom: 8px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__submit {
    background-color: #4057FF;
    border-radius: 4px;
    border: none;
    padding: 6px 18px;
    color: #fff;
    cursor: pointer;
    align-self: flex-end;
    transition: background-color .2s ease;
    &:hover {
      background-color: lighten(#4057FF, 5%);
    }
    &:active {
      background-color: darken(#4057FF, 5%);
    }
  }
}